@charset "UTF-8";
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

:root {
  --primary-color: #a35df6;
  --primary-light: #4f5f93;
  --primary-dark: #1d2238;
  --secondary-color: #252d47;
  --grey-color: #eee;
  --white-color: #fefefe;
  --base-height: 50px;
  --error-color: #b3255d; }

::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400; }

::-moz-placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400; }

:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400; }

:-moz-placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #383346 inset;
  -webkit-text-fill-color: white !important; }

body {
  position: relative;
  font-family: 'Assistant', sans-serif;
  background: linear-gradient(135deg, var(--primary-light) 0%, var(--primary-dark) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between; }

p {
  font-size: 18px;
  line-height: 1.8; }

table {
  text-align: center;
  max-width: 840px;
  margin: 0 auto;
  opacity: 0;
  transition: 0.8s 3s ease-in-out; }
  table h4 {
    margin: 0;
    font-weight: 300;
    min-height: 40px;
    color: rgba(255, 255, 255, 0.6); }

table tr {
  display: flex;
  flex-wrap: wrap; }

table td {
  border: 1px solid #544e618c;
  flex: 1 0 140px;
  padding: 20px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

table td span:first-of-type {
  font-size: 25px; }

table td span:last-of-type {
  font-size: 32px;
  font-weight: 700; }

.menu {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 30px auto;
  padding: 0; }

.menu a {
  display: block;
  height: 50px;
  padding: 8px 12px;
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 700;
  font-size: 28px; }
  .menu a span {
    display: block;
    height: 0px;
    overflow: hidden;
    transition: 1s 2s ease-in-out; }

.menu.is-active a span {
  height: 50px; }

.page {
  max-width: 1000px;
  flex: 0 0 80%; }
  .page__title {
    font-size: 60px; }
    .page__title a {
      text-decoration: none;
      color: var(--white-color);
      text-shadow: 0 0 40px rgba(0, 0, 0, 0.5); }
  .page__container {
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center; }

.page__content {
  text-align: center; }

.report,
.error {
  display: none; }

.focusline {
  position: absolute;
  top: 100%;
  left: 0;
  display: block;
  width: 0;
  height: 2px;
  background: #793585;
  transition: width 0.3s ease-in-out; }

form {
  position: relative;
  display: flex;
  justify-content: space-between; }

.button {
  flex: var(--base-height);
  max-width: var(--base-height);
  background: rgba(255, 255, 255, 0.07);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border-radius: 0px 10px 10px 0px;
  box-shadow: 3px 3px 3px 0 rgba(0, 0, 0, 0.8); }
  .button:focus {
    outline: none;
    box-shadow: 0 0 12px -2px white; }
  .button svg {
    width: 50%;
    height: 50%; }

[type=color], [type=date], [type=datetime-local], [type=datetime], [type=email], [type=month], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=time], [type=url], [type=week], textarea, select {
  height: var(--base-height);
  flex: 70%;
  border: none;
  background: rgba(255, 255, 255, 0.07);
  color: var(--white-color);
  padding-left: 10px;
  border-radius: 10px 0px 0px 10px; }
  [type=color]:focus, [type=date]:focus, [type=datetime-local]:focus, [type=datetime]:focus, [type=email]:focus, [type=month]:focus, [type=number]:focus, [type=password]:focus, [type=search]:focus, [type=tel]:focus, [type=text]:focus, [type=time]:focus, [type=url]:focus, [type=week]:focus, textarea:focus, select:focus {
    outline: none; }
  [type=color]:focus ~ .focusline, [type=date]:focus ~ .focusline, [type=datetime-local]:focus ~ .focusline, [type=datetime]:focus ~ .focusline, [type=email]:focus ~ .focusline, [type=month]:focus ~ .focusline, [type=number]:focus ~ .focusline, [type=password]:focus ~ .focusline, [type=search]:focus ~ .focusline, [type=tel]:focus ~ .focusline, [type=text]:focus ~ .focusline, [type=time]:focus ~ .focusline, [type=url]:focus ~ .focusline, [type=week]:focus ~ .focusline, textarea:focus ~ .focusline, select:focus ~ .focusline {
    width: 100%;
    transition: width 0.1s ease; }

.error {
  position: absolute;
  top: 115%;
  left: 10px;
  font-size: 14px;
  color: var(--error-color); }

.status {
  position: relative;
  font-weight: 700;
  font-size: 14px; }
  .status::before {
    content: 'ðŸ‘'; }
  .status--ready::before {
    content: 'ðŸ’ª'; }

.report {
  pointer-events: none;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  width: 100vw;
  min-height: 100%;
  transform: scale(0);
  transition: all 2s 0s ease;
  z-index: 12;
  overflow: scroll;
  background: var(--primary-color);
  color: var(--grey-color); }

.report__header,
.report__footer {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  opacity: 0;
  transition: 0.8s 1.3s ease-in-out; }

.report__header {
  padding: 30px 0;
  margin-bottom: 30px;
  background: rgba(255, 255, 255, 0.01); }
  .report__header h3 {
    font-size: 22px;
    font-weight: 300; }

.report__footer {
  padding: 30px 0; }

.report__button {
  color: var(--white-color);
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  margin: 0 15px; }

.report.is-active {
  pointer-events: auto;
  transform: scale(1); }
  .report.is-active table {
    opacity: 1;
    transition: 0.8s 3s ease-in-out; }
  .report.is-active .report__header,
  .report.is-active .report__footer {
    opacity: 1;
    transition: 0.8s 2s ease-in-out; }

.score {
  display: block;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center; }

.is-sending .split__fade {
  opacity: 1;
  transition: 0.8s 1.2s ease-in-out; }

.is-sending .report {
  pointer-events: auto; }

.is-sending::after, .is-sending::before {
  width: 100%; }

.footer {
  padding: 30px 0;
  opacity: 0.3; }

.loading {
  position: absolute;
  z-index: 66;
  top: 30%;
  left: 0;
  width: 100%;
  text-align: center;
  opacity: 0;
  height: 0;
  transition: opacity 0.8s 0s;
  pointer-events: none; }
  .loading h5 {
    color: var(--white-color);
    font-size: 26px;
    font-weight: 300; }
  .loading h5 span {
    display: block;
    margin: 10px 0;
    opacity: 0.4;
    font-size: 14px; }

.loading.is-active {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 1;
  transition: opacity 0.8s 1.5s; }

.loading__animation {
  margin: 40px auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  filter: url(#goo);
  width: 200px;
  height: 100px; }

.loading__animation div {
  background: #fffff0;
  width: 20px;
  height: 20px;
  border-radius: 50%; }

.loading__animation > div {
  transform: translateX(0);
  animation: sideways 2.5s infinite ease; }

.loading__animation > div:nth-child(1) {
  animation-delay: .25s; }

.loading__animation > div:nth-child(2) {
  animation-delay: .5s; }

.loading__animation > div:nth-child(3) {
  animation-delay: .75s; }

.loading__animation > div:nth-child(4) {
  animation-delay: 1s; }

.loading__animation > div:nth-child(5) {
  animation-delay: 1.25s; }

@keyframes circle {
  50% {
    width: 25px;
    height: 25px; }
  100% {
    transform: rotate(360deg); } }

@keyframes sideways {
  33% {
    transform: translateX(50px); }
  66% {
    transform: translateX(-50px); } }

.dialog {
  max-width: 600px;
  border: none;
  box-shadow: 0px 0px 20px 0px #00000078;
  text-align: center; }
  .dialog h4 {
    color: var(--error-color);
    font-size: 28px;
    margin-top: 0; }
  .dialog menu {
    padding-left: 0; }

.dialog__button {
  background: #261f33;
  color: white;
  border: none;
  padding: 12px 25px;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 700; }

.dialog__content strong {
  margin-right: 10px;
  text-transform: uppercase;
  font-size: 80%;
  opacity: 0.8; }

.audit {
  max-width: 816px;
  margin: 100px auto 0;
  display: flex;
  flex-direction: column; }

.audit__item--pass .summary {
  color: #51bd51; }

.audit__item--fail .summary {
  color: #f44336; }

.audit__score {
  float: right;
  font-size: 26px;
  font-weight: 700;
  margin: 0 0 20px 20px; }

.summary {
  display: flex;
  align-items: center; }
  .summary p {
    font-size: 22px;
    margin: 0; }
